import React from "react";
import "../styles/VideoItem.css";

class VideoItem extends React.Component {
  onVideoSelect() {
    this.props.onVideoClick(this.props);
  }
  render() {
    return (
      <div onClick={this.onVideoSelect.bind(this)} class="list__item">
        <div class="list__img">
          <img id="img" class="style-scope yt-img-shadow" alt="" width="168" src={this.props.img} />
        </div>
        <div class="list__detail">
          <h5 class="list__title">{this.props.title}</h5>
          <div class="list__creator">{this.props.channel}</div>
          <div class="list__metrics">{this.props.time}</div>
        </div>
      </div>
    );
  }
}

export default VideoItem;
