import React from "react";
import VideoContent from "./VideoContent";
import VideoDescription from "./VideoDescription";
import "./styles/VideoDetail.css";

class VideoDetail extends React.Component {
  render() {
    console.log(this.props.currentVideoData);
    return (
      <div className="video">
        <VideoContent videoID={this.props.currentVideoData.id} />
        <VideoDescription
          videoTitle={this.props.currentVideoData.title}
          videoDescription={this.props.currentVideoData.description}
        />
      </div>
    );
  }
}

export default VideoDetail;
