import React from "react";
import Header from "./Header/Header";
import "./styles/App.css";
import VideoDetail from "./VideoDetail";
import VideoList from "./VideoList/VideoList";
import * as youtube from "../apis/youtube";

class App extends React.Component {
  state = { term: "", queryResult: [], currentVideo: {} };

  componentDidMount() {
    this.onFormSubmit("microsoft surface");
  }

  async onFormSubmit(term) {
    await this.setState({ term });
    await this.generateVideoList();
    await this.setState({ currentVideo: this.state.queryResult[0] });
  }

  onVideoClick(videoData) {
    this.setState({ currentVideo: videoData });
  }

  async generateVideoList() {
    const queryObject = await youtube.getAuthentication(this.state.term);
    console.log(queryObject);
    await this.setState({
      queryResult: queryObject.items.map((ele) => {
        return {
          channel: ele.snippet.channelTitle,
          description: ele.snippet.description,
          title: ele.snippet.title,
          img: ele.snippet.thumbnails.default.url,
          time: ele.snippet.publishTime,
          id: ele.id.videoId,
        };
      }),
    });
  }

  render() {
    return (
      <div className="container">
        <Header onFormSubmit={this.onFormSubmit.bind(this)} />
        <VideoDetail currentVideoData={this.state.currentVideo} />
        <VideoList
          onVideoClick={this.onVideoClick.bind(this)}
          queryResult={this.state.queryResult}
        />
      </div>
    );
  }
}

export default App;
