import React from "react";
import Icon from "./Icon";
import Profile from "./Profile";
import "../styles/Nav.css";

class Nav extends React.Component {
  render() {
    return (
      <div class="nav">
        <Icon iconType="iconCreate" />
        <Icon iconType="iconGrid" />
        <Icon iconType="iconNotification" />
        <Profile />
      </div>
    );
  }
}

export default Nav;
