import React from "react";
import Icon from "./Icon";
import "../styles/Logo.css";

class Logo extends React.Component {
  render() {
    return (
      <div className="logo">
        <Icon className="logo__menu" iconType="iconMenu" />
        <Icon className="logo__YT" iconType="iconYT" />
      </div>
    );
  }
}

export default Logo;
